.event-hami-video {
    @extend .event-block;
    &__title {
        @extend .event-title;
    }
    &__list {
        @extend .event-list;
        flex-wrap: wrap;
        li {
            flex-basis: 50%;
            padding: 0 1rem;
            display: flex;
            align-items: center;
            margin-bottom: 1rem;
            @include susy-breakpoint($s-320, $c8) {
                flex-basis: 100%;
                margin-bottom: 1rem;
                align-items: flex-start;
            }
            @include susy-breakpoint($s-768, $c12) {
                flex-basis: 100%;
                margin-bottom: 1rem;
                align-items: flex-start;
            }
            span {
                font-size: 2.4rem;
                line-height: 1.6;
                color: #fff;
                @include susy-breakpoint($s-320, $c8) {
                    font-size: 1.4rem;
                    padding-top: 0.3rem;
                }
                @include susy-breakpoint($s-768, $c12) {
                    font-size: 1.6rem;
                    padding-top: 0.3rem;
                }
                @include susy-breakpoint($s-960, $c12) {
                    font-size: 1.8rem;
                }
                @include susy-breakpoint($s-1201, $c18) {
                    font-size: 1.8rem;
                }
            }
            &:nth-child(3) {
                flex-basis: 100%;
                margin-bottom: 0;
            }
        }
    }
    &__label {
        @extend .site-label;
    }
    &__remind {
        margin-bottom: 4rem;
        background: rgb(228,208,114);
        background: linear-gradient(90deg, rgba(228,208,114,1) 0%, rgba(232,226,152,1) 30%, rgba(236,196,107,1) 100%);
        display: flex;
        border-radius: 10rem;
        align-items: center;
        justify-content: center;
        padding: 1.6rem;
        @include susy-breakpoint($s-320, $c8) {
            border-radius: 1.6rem;
            padding: 1.2rem;
        }
        span {
            font-size: 2.4rem;
            color: #000000;
            text-align: center;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 1.8rem;
            }
        }
    }
    &__remind-border {
        text-align: center;
        margin-bottom: 2rem;
        span {
            display: inline-block;
            background: none;
            padding: 0.4rem 4rem;
            border: 0.4rem solid;
            font-size: 2.4rem;
            color: #eaca69;
            border-radius: 4rem;
            line-height: 1.6;
            background-origin: border-box;
            background-clip: content-box, border-box;
            strong {
                color: #99f538;
            }
            @include susy-breakpoint($s-320, $c8) {
                border-radius: 1rem;
                border-width: 0.2rem;
                font-size: 1.6rem;
                padding-left: 1rem;
                padding-right: 1rem;
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 2rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 2rem;
            }
        }
    }
    &__content {
        @extend .event-wrap;
        display: block;
        padding-bottom: 4rem;
    }
    &__plan {
        padding: 0 4.8rem;
        flex: 1;
        @include susy-breakpoint($s-320, $c8) {
            flex-basis: 100%;
            padding-left: 0;
            padding-right: 0;
        }
        &:nth-child(1) {
            position: relative;
            margin-bottom: 2rem;
            padding-bottom: 2rem;
        }
        &:only-child {
            margin-bottom: 0;
            padding-bottom: 0;
        }
        & > img {
            height: 4.3rem;
            display: block;
            margin: 0 auto 2rem;
            @include susy-breakpoint($s-320, $c8) {
                height: 2.4rem;
            }
        }
        h3 {
            line-height: 1.6;
            font-size: 3rem;
            color: #fff;
            text-align: center;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 2rem;
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 2rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 2.4rem;
            }
        }
        h4 {
            font-size: 3rem;
            color: #fff;
            line-height: 1.6;
            text-align: center;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 2rem;
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 2rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 2.4rem;
            }
            span {
                color: #ed3013;
            }
        }
    }
    &__point {
        display: flex;
        justify-content: center;
        position: relative;
        margin-bottom: 1rem;
        flex-wrap: wrap;
        @include susy-breakpoint($s-320, $c8) {
            margin-left: -2.4rem;
        }
        p {
            flex-basis: 100%;
            position: relative;
            display: inline-block;
            margin-bottom: 0;
            text-align: center;
            @include susy-breakpoint($s-320, $c8) {
                margin-bottom: 0.4rem;
            }
            & > small {
                vertical-align: baseline;
            }
            > i {
                font-size: 2rem;
                color: #fff;
                display: inline-block;
                line-height: 1;
                font-style: normal;
                padding-left: 0.4rem;
                text-align: center;
                @include susy-breakpoint($s-320, $c8) {
                    font-size: 1.6rem;
                }
            }
            > b {
                font-family: 'Anton', sans-serif;
                font-size: 4.8rem;
                color: #fff;
                letter-spacing: 0.2rem;
                small {
                    font-size: 4.8rem;
                    vertical-align: baseline;
                }
                @include susy-breakpoint($s-320, $c8) {
                    font-size: 2rem;
                    small {
                        font-size: 1.8rem;
                    }
                }
                &.ct {
                    font-family: initial;
                }
            }
            > span {
                font-family: 'Anton', sans-serif;
                font-size: 17rem;
                background: rgb(236,195,107);
                background: linear-gradient(111deg, rgba(236,195,107,1) 0%, rgba(243,238,171,1) 50%, rgba(239,232,157,1) 100%);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                font-weight: normal;
                position: relative;
                -webkit-text-stroke: 2px #907331;
                line-height: 1.2;
                & > small {
                    font-size: 6rem;
                    color: #fff;
                    background: #fff;
                    -webkit-background-clip: inherit;
                    -webkit-text-stroke: 0;
                    vertical-align: baseline;
                    @include susy-breakpoint($s-320, $c8) {
                        font-size: 3rem;
                    }
                }
                &.sm {
                    font-size: 11rem;
                    & > small {
                        font-size: 4.8rem;
                    }
                    @include susy-breakpoint($s-320, $c8) {
                        font-size: 6rem;
                        & > small {
                            font-size: 2rem;
                        }
                    }
                    @include susy-breakpoint($s-960, $c12) {
                        font-size: 8rem;
                        & > small {
                            font-size: 3rem;
                        }
                    }
                }
                @include susy-breakpoint($s-320, $c8) {
                    font-size: 8rem;
                }
                @include susy-breakpoint($s-960, $c12) {
                    font-size: 14rem;
                }
            }
        }
    }
    &__month-point {
        position: absolute;
        left: 50%;
        transform: translate3d(-225%,28%,0);
        top: 50%;
        @include susy-breakpoint($s-320, $c8) {
            transform: translate3d(-188%,26%,0);
        }
        @include susy-breakpoint($s-960, $c12) {
            transform: translate3d(-213%,9%,0);
        }
        > small {
            display: inline-block;
            font-size: 2rem;
            color: #fff;
            font-family: 'Anton', sans-serif;
            vertical-align: baseline;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
            }
        }
        > span {
            display: inline-block;
            font-size: 4.4rem;
            color: #fff;
            font-family: 'Anton', sans-serif;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 2rem;
            }
        }
        > b {
            display: inline-block;
            font-size: 3rem;
            color: #fff;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
            }
        }
    }
    &__remarks {
        color: #ffae00;
        font-size: 2.2rem;
        line-height: 1.6;
        text-align: center;
        margin-bottom: 2rem;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.4rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: 1.8rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 2rem;
        }
    }
    &__rule {
        @extend .rule;
        border-bottom: 0;
        h3 {
            @extend .rule-title;
        }
        ol {
            @extend .site-rule;
        }
    }
    &__link-wrap {
        display: flex;
        margin-bottom: 4rem;
        position: relative;
        padding-top: 4rem;
        justify-content: center;
        &::after {
            position: absolute;
            width: 100%;
            height: 0.2rem;
            background: linear-gradient(90deg, rgba(234,227,150,0) 0%, rgba(234,227,150,1) 30%, rgba(234,227,150,1) 50%, rgba(234,227,150,1) 70%, rgba(234,227,150,0) 100%);
            top: 0;
            left: 0;
            content: "";
        }
        @include susy-breakpoint($s-320, $c8) {
            flex-wrap: wrap;
            padding-top: 2rem;
            margin-bottom: 2rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            flex-wrap: wrap;
        }
        @include susy-breakpoint($s-960, $c12) {
            flex-wrap: wrap;
        }
        .btn-link {
            width: 22rem;
            font-size: 2.2rem;
            margin-left: 2rem;
            margin-right: 2rem;
            @include susy-breakpoint($s-320, $c8) {
                width: 22rem;
                height: 5rem;
                font-size: 2rem;
                &:nth-child(1) {
                    margin-bottom: 2rem;
                }
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 2rem;
                &:nth-child(1) {
                    margin-bottom: 2rem;
                }
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 2rem;
                width: 22rem;
                &:nth-child(1) {
                    margin-bottom: 2rem;
                }
            }
        }
    }
    &__inner-ol-list {
        list-style: none;
        margin: 0;
        padding: 0;
        counter-reset: inner-ol-list;
        display: grid;
        width: 100%;
        grid-template-columns: repeat(2, 1fr);
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: repeat(1, 1fr);
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-template-columns: repeat(1, 1fr);
        }
        > li {
            flex-basis: 45%;
            counter-increment: inner-ol-list;
            padding-left: 4rem;
            position: relative;
            font-size: 2.4rem;
            color: #fff;
            line-height: 1.6;
            &:not(:last-child) {
                margin-bottom: 1rem;
            }
            &:nth-child(1) {
                order: 1;
            }
            &:nth-child(2) {
                order: 3;
            }
            &:nth-child(3) {
                order: 2;
            }
            &:nth-child(4) {
                order: 4;
            }
            &::before {
                position: absolute;
                content: counter(inner-ol-list);
                font-family: 'Anton', sans-serif;
                font-style: italic;
                color: #000;
                font-size: 2.4rem;
                width: 3.6rem;
                height: 3.6rem;
                border-radius: 50%;
                background-color: #ffdb6f;
                top: 0.2rem;
                left: 0;
                display: flex;
                align-items: center;
                box-sizing: border-box;
                padding-left: 1rem;
            }
            @include susy-breakpoint($s-320, $c8) {
                flex-basis: 100%;
                font-size: 1.4rem;
                padding-left: 2.4rem;
                &:not(:last-child) {
                    margin-bottom: 2rem;
                }
                &::before {
                    font-size: 1.6rem;
                    width: 2rem;
                    height: 2rem;
                    top: 0.2rem;
                    padding-left: 0.4rem;
                }
            }
            @include susy-breakpoint($s-768, $c12) {
                flex-basis: 100%;
                font-size: 1.8rem;
                padding-left: 3rem;
                &:not(:last-child) {
                    margin-bottom: 4rem;
                }
                &::before {
                    font-size: 1.6rem;
                    width: 2.4rem;
                    height: 2.4rem;
                    top: 0.2rem;
                    padding-left: 0.6rem;
                    background-size: 1.4rem * 0.8 1.6rem * 0.8;
                }
            }
            @include susy-breakpoint($s-960, $c12) {
                flex-basis: 50%;
                font-size: 1.8rem;
                padding-left: 3rem;
                &::before {
                    font-size: 1.6rem;
                    width: 2.4rem;
                    height: 2.4rem;
                    top: 0.2rem;
                    padding-left: 0.6rem;
                    background-size: 1.4rem * 0.8 1.6rem * 0.8;
                }
            }
            > span {
                &.text-color-green {
                    color: #96f037;
                }
                &.text-color-yellow {
                    color: #ffdb6f;
                }
            }
            > small {
                vertical-align: baseline;
                font-size: 2rem;
                color: #ff0000;
            }
        }
    }
    &__btn-signup {
        flex-basis: 100%;
        display: flex;
        box-sizing: border-box;
        position: relative;
        margin-right: 0;
        justify-content: center;
        span {
            display: flex;
            align-items: center;
            font-size: 2rem;
            font-weight: 700;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.6rem;
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 1.6rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 1.8rem;
            }
            &:nth-child(1) {
                border: solid 0.2rem #ffdb6f;
                color: #ffdb6f;
                padding: 1rem 3rem;
                @include susy-breakpoint($s-320, $c8) {
                    padding: 0.6rem 1.2rem;
                }
            }
            &:nth-child(2) {
                border: solid 0.2rem #ffdb6f;
                background-color: #ffdb6f;
                color: #000;
                padding: 1rem 4rem 1rem 3rem;
                position: relative;
                @include susy-breakpoint($s-320, $c8) {
                    padding: 0.6rem 2.4rem 0.6rem 1.2rem;
                }
                &::after {
                    position: absolute;
                    content: "";
                    width: 1.4rem;
                    height: 1.9rem;
                    right: 2rem;
                    top: 50%;
                    transform: translate3d(0,-50%,0);
                    content: "";
                    background: transparent url('../images/icon-arrow-right.svg') no-repeat 0 center;
                    background-size: 100%;
                    transition: all 300ms ease;
                    @include susy-breakpoint($s-320, $c8) {
                        width: 1.4rem * 0.8;
                        height: 1.9rem * 0.8;
                        right: 1rem;
                    }
                }
            }
        }
        &:hover {
            text-decoration: none;
            span {
                &:nth-child(2) {
                    &::after {
                        transform: translate3d(30%,-50%,0);
                    }
                }
            }
        }
    }
    &__btn-store-locator {
        flex-basis: 100%;
        display: flex;
        box-sizing: border-box;
        position: relative;
        margin-right: 0;
        justify-content: center;
        span {
            display: flex;
            align-items: center;
            font-size: 2rem;
            font-weight: 700;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.6rem;
            }
            &:nth-child(1) {
                border: solid 0.2rem #ffdb6f;
                background-color: #ffdb6f;
                color: #000;
                padding: 1rem 4rem 1rem 3rem;
                position: relative;
                @include susy-breakpoint($s-320, $c8) {
                    padding: 0.6rem 4rem 0.6rem 2rem;
                }
                &::after {
                    position: absolute;
                    content: "";
                    width: 1.4rem;
                    height: 1.9rem;
                    right: 2rem;
                    top: 50%;
                    transform: translate3d(0,-50%,0);
                    content: "";
                    background: transparent url('../images/icon-arrow-right.svg') no-repeat 0 center;
                    background-size: 100%;
                    transition: all 300ms ease;
                    @include susy-breakpoint($s-320, $c8) {
                        width: 1.4rem * 0.8;
                        height: 1.9rem * 0.8;
                    }
                }
            }
        }
        &:hover {
            text-decoration: none;
            span {
                &:nth-child(1) {
                    &::after {
                        transform: translate3d(30%,-50%,0);
                    }
                }
            }
        }
    }
    &__tokyo-detail-ul-list {
        margin: 0 auto;
        padding: 0;
        list-style: none;
        width: 56rem;
        @include susy-breakpoint($s-320, $c8) {
            width: auto;
        }
        li {
            padding-left: 4rem;
            position: relative;
            font-size: 2.4rem;
            color: #fff;
            line-height: 1.6;
            &:not(:last-child) {
                margin-bottom: 1rem;
            }
            &::before {
                content: "";
                position: absolute;
                width: 3.6rem;
                height: 3.6rem;
                border-radius: 50%;
                background: #99f538 url("../images/icon-arrow-right-full.svg") no-repeat center center;
                background-size: 1.4rem 1.6rem;
                top: 0.2rem;
                left: 0;
                display: flex;
                align-items: center;
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
                padding-left: 2.4rem;
                &::before {
                    width: 2rem;
                    height: 2rem;
                    background-size: 1.4rem * 0.6 1.6rem * 0.6;
                }
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 1.8rem;
                padding-left: 3rem;
                &::before {
                    width: 2.4rem;
                    height: 2.4rem;
                    top: 0.2rem;
                    background-size: 1.4rem * 0.6 1.6rem * 0.8;
                }
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 1.8rem;
                padding-left: 3rem;
                &::before {
                    width: 2.4rem;
                    height: 2.4rem;
                    top: 0.2rem;
                    background-size: 1.4rem * 0.6 1.6rem * 0.8;
                }
            }
        }
    }
    &__vr-detail-ol-list {
        list-style: none;
        margin: 0 auto 4rem;
        padding: 0;
        counter-reset: inner-ol-list;
        width: 68rem;
        @include susy-breakpoint($s-320, $c8) {
            width: auto;
            margin-bottom: 2rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            width: auto;
        }
        > li {
            counter-increment: inner-ol-list;
            padding-left: 4rem;
            position: relative;
            font-size: 2.4rem;
            color: #fff;
            line-height: 1.6;
            &:not(:last-child) {
                margin-bottom: 1rem;
            }
            &::before {
                position: absolute;
                content: counter(inner-ol-list);
                font-family: 'Anton', sans-serif;
                font-style: italic;
                color: #000;
                font-size: 2.4rem;
                width: 3.6rem;
                height: 3.6rem;
                border-radius: 50%;
                background-color: #ffdb6f;
                top: 0.2rem;
                left: 0;
                display: flex;
                align-items: center;
                box-sizing: border-box;
                padding-left: 1rem;
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
                padding-left: 2.4rem;
                &::before {
                    font-size: 1.6rem;
                    width: 2rem;
                    height: 2rem;
                    background-size: 1.4rem * 0.6 1.6rem * 0.6;
                    padding-left: 0.4rem;
                }
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 1.8rem;
                padding-left: 3rem;
                &::before {
                    font-size: 1.6rem;
                    width: 2.4rem;
                    height: 2.4rem;
                    top: 0.2rem;
                    padding-left: 0.6rem;
                }
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 1.8rem;
                padding-left: 3rem;
                &::before {
                    font-size: 1.6rem;
                    width: 2.4rem;
                    height: 2.4rem;
                    top: 0.2rem;
                    padding-left: 0.6rem;
                }
            }
        }
    }
    &__vr-detail-inner-ul-list {
        margin: 2rem auto 0;
        padding: 2rem 3rem;
        list-style: none;
        width: 58rem;
        border: solid 0.2rem #ffdb6f;
        box-sizing: border-box;
        @include susy-breakpoint($s-320, $c8) {
            width: auto;
            padding: 1rem 2rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            width: auto;
        }
        li {
            padding-left: 4rem;
            position: relative;
            font-size: 2.4rem;
            color: #fff;
            line-height: 1.6;
            &:not(:last-child) {
                margin-bottom: 1rem;
            }
            &::before {
                content: "";
                position: absolute;
