                background-size: 1.4rem 1.6rem;
                top: 0.2rem;
                left: 0;
                display: flex;
                align-items: center;
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
                padding-left: 2.4rem;
                &::before {
                    width: 2rem;
                    height: 2rem;
                    background-size: 1.4rem * 0.6 1.6rem * 0.6;
                }
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 1.8rem;
                padding-left: 3rem;
                &::before {
                    width: 2.4rem;
                    height: 2.4rem;
                    background-size: 1.4rem * 0.6 1.6rem * 0.6;
                }
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 1.8rem;
                padding-left: 3rem;
                &::before {
                    font-size: 1.6rem;
                    width: 2.4rem;
                    height: 2.4rem;
                    top: 0.2rem;
