.block-kv {
    background: #000 url('../images/block-kv-bg.jpg') no-repeat center top;
    background-size: 100% auto;
    padding-bottom: 44.2%;
    position: relative;
    overflow: hidden;
    @include susy-breakpoint($s-320, $c8) {
        padding-bottom: 161.5%;
        background-image: url('../images/block-kv-bg-m.jpg');
    }
    &__logo {
        position: absolute;
        width: 12%;
        left: 6%;
        top: 11%;
        transform: translate3d(0,0,0);
        z-index: 10;
        @include susy-breakpoint($s-320, $c8) {
            display: none;
        }
    }
    &__icon {
        position: absolute;
        width: 13%;
        left: 3.2%;
        bottom: 3.6%;
        transform: translate3d(0,0,0);
        z-index: 10;
        @include susy-breakpoint($s-320, $c8) {
            width: 94%;
            left: 50%;
            bottom: 2.6%;
            transform: translate3d(-50%,0,0);
            z-index: 40;
        }
        @include susy-breakpoint($s-1201, $c18) {
            bottom: 6%;
        }
    }
    &__main-title {
        position: absolute;
        width: 40%;
        left: 50%;
        top: 50%;
        transform: translate3d(-71%,-57%,0);
        z-index: 50;
        @include susy-breakpoint($s-320, $c8) {
            width: 70%;
            transform: translate3d(-66%,-45%,0);
            z-index: 40;
        }
    }
    &__sub-title {
        position: absolute;
        width: 26%;
        left: 50%;
        top: 50%;
        transform: translate3d(-88%,70%,0);
        z-index: 40;
        @include susy-breakpoint($s-320, $c8) {
            width: 69%;
            transform: translate3d(-61%,123%,0);
        }
    }
    &__main-title-bg {
        position: absolute;
        width: 90%;
        left: 50%;
        top: 50%;
        transform: translate3d(-56%,-34%,0);
        z-index: 30;
        @include susy-breakpoint($s-320, $c8) {
            width: 210%;
            transform: translate3d(-56%,-1%,0);
        }
    }
    &__sub-title-bg {
        position: absolute;
        width: 89%;
        left: 50%;
        top: 50%;
        transform: translate3d(-48%,-11%,0);
        z-index: 30;
        @include susy-breakpoint($s-320, $c8) {
            width: 234%;
            transform: translate3d(-38%,9%,0);
        }
    }
    &__TaiTzuYing {
        width: 26%;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate3d(12%,33%,0);
        z-index: 45;
        @include susy-breakpoint($s-320, $c8) {
            width: 62%;
            transform: translate3d(-26%,1%,0);
            z-index: 20;
        }
    }
    &__signature {
        z-index: 10;
        position: absolute;
        width: 5.6%;
        left: 50%;
        top: 50%;
        transform: translate3d(89%,255%,0);
        @include susy-breakpoint($s-320, $c8) {
            width: 14%;
            transform: translate3d(222%,-45%,0);
        }
    }
    &__flash-bg {
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 0;
        background-color: rgba(255,255,255,0.2);
        left: 0;
        top: 0;
        mix-blend-mode: overlay;
    }
}