.event-mod {
    @extend .event-block;
    &__title {
        @extend .event-title;
    }
    &__list {
        @extend .event-list;
        li {
            padding: 0 1rem;
            display: flex;
            align-items: center;
            @include susy-breakpoint($s-320, $c8) {
                flex-basis: 100%;
                justify-content: center;
            }
            span {
                font-size: 2.4rem;
                line-height: 1.6;
                color: #fff;
                @include susy-breakpoint($s-320, $c8) {
                    font-size: 1.4rem;
                }
                @include susy-breakpoint($s-768, $c12) {
                    font-size: 1.6rem;
                }
                @include susy-breakpoint($s-960, $c12) {
                    font-size: 1.8rem;
                }
                @include susy-breakpoint($s-1201, $c18) {
                    font-size: 1.8rem;
                }
            }
        }
    }
    &__label {
        @extend .site-label;
    }
    &__remind {
        text-align: center;
        margin-bottom: 4rem;
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 2rem;
        }
        span {
            display: inline-block;
            background: none;
            padding: 0.4rem 2rem;
            border: 0.4rem solid;
            font-size: 2.4rem;
            color: #eaca69;
            border-radius: 4rem;
            line-height: 1.6;
            background-origin: border-box;
            background-clip: content-box, border-box;
            strong {
                color: #99f538;
            }
            @include susy-breakpoint($s-320, $c8) {
                border-radius: 1rem;
                border-width: 0.2rem;
                font-size: 1.6rem;
                padding-left: 1rem;
                padding-right: 1rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 2rem;
            }
        }
    }
    &__content {
        @extend .event-wrap;
    }
    &__plan {
        flex: 1;
        & > img {
            height: 6rem;
            display: block;
            margin: 0 auto 2rem;
            @include susy-breakpoint($s-320, $c8) {
                height: 3rem;
            }
        }
        h3 {
            font-size: 3rem;
            color: #ffdb6f;
            text-align: center;
            strong {
                color: #99f538;
            }
        }
    }
    &__remarks {
        color: #ffae00;
        font-size: 2.2rem;
        line-height: 1.6;
        text-align: center;
        margin-bottom: 2rem;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.4rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: 1.8rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 2rem;
        }
    }
    &__rule {
        @extend .rule;
        h3 {
            @extend .rule-title;
        }
        ol {
            @extend .site-rule;
        }
    }
    &__item {
        display: flex;
        padding: 2rem 2rem 0.4rem;
        margin-bottom: 1rem;
        position: relative;
        &::after {
            position: absolute;
            width: 100%;
            height: 0.1rem;
            background: linear-gradient(90deg, rgba(234,227,150,0) 0%, rgba(234,227,150,1) 30%, rgba(234,227,150,1) 50%, rgba(234,227,150,1) 70%, rgba(234,227,150,0) 100%);
            bottom: 0;
            left: 0;
            content: "";
        }
        @include susy-breakpoint($s-320, $c8) {
            padding-left: 1rem;
            padding-right: 1rem;
            padding-bottom: 2rem;
            margin-bottom: 2rem;
            flex-wrap: wrap;
            padding-top: 0;
        }
    }
    &__name {
        flex: 1;
        font-size: 3rem;
        color: #ffdb6f;
        line-height: 1.6;
        small {
            color: #fff;
            font-size: 2rem;
            vertical-align: middle;
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: 2rem;
            flex-basis: 100%;
            text-align: center;
            small {
                font-size: 1.6rem;
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: 1.8rem;
            small {
                font-size: 1.4rem;
            }
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 2rem;
            small {
                font-size: 1.6rem;
            }
        }
    }
    &__point {
        flex: 1;
        font-size: 3rem;
        text-align: left;
        display: flex;
        position: relative;
        align-items: center;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 2rem;
            flex-basis: 29rem;
            justify-content: center;
            flex-wrap: wrap;
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: 1.8rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 2rem;
        }
        & > img {
            height: 2.4rem;
            display: inline-block;
            vertical-align: middle;
            @include susy-breakpoint($s-320, $c8) {
                height: 1.8rem;
                margin-right: 0.4rem;
            }
        }
        strong {
            color: #99f538;
            padding-left: 0.8rem;
        }
        p {
            position: absolute;
            left: 35rem;
            bottom: -0.2rem;
            margin-bottom: 0;
            @include susy-breakpoint($s-320, $c8) {
                flex-basis: 100%;
                left: inherit;
                bottom: inherit;
                position: relative;
                text-align: center;
            }
            @include susy-breakpoint($s-768, $c12) {
                left: 23rem;
                bottom: 0;
            }
            @include susy-breakpoint($s-960, $c12) {
                left: 25rem;
                bottom: 0;
            }
            &::after {
                content: "點";
                font-size: 3rem;
                color: #fff;
                position: absolute;
                top: 2.6rem;
                right: -3.4rem;
                @include susy-breakpoint($s-320, $c8) {
                    position: relative;
                    right: 0;
                    top: 0;
                    font-size: 1.8rem;
                    padding-left: 0.8rem;
                }
                @include susy-breakpoint($s-768, $c12) {
                    font-size: 1.6rem;
                    top: 2rem;
                    right: -2.4rem;
                }
                @include susy-breakpoint($s-960, $c12) {
                    font-size: 2rem;
                    top: 2rem;
                    right: -2.4rem;
                }
            }
        }
        span {
            font-family: 'Anton', sans-serif;
            font-size: 6.5rem;
            background: rgb(236,195,107);
            background: linear-gradient(111deg, rgba(236,195,107,1) 0%, rgba(243,238,171,1) 50%, rgba(239,232,157,1) 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            font-weight: normal;
            position: relative;
            -webkit-text-stroke: 2px #907331;
            line-height: 1;
            @include susy-breakpoint($s-768, $c12) {
                font-size: 4rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 4.8rem;
            }
        }
    }
}