.s-nav {
    background-color: rgba(0,0,0,0.6);
    width: 100%;
    height: 8rem;
    position: absolute;
    left: 0;
    top: 9.9rem;
    z-index: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    &.is-fixed {
        top: 0;
        position: fixed;
    }
    @include susy-breakpoint($s-320, $c8) {
        background-color: rgba(0,0,0,0.9);
        top: 6rem;
        height: 6rem;
        position: fixed;
        overflow-x: scroll;
        overflow-y: hidden;
        white-space: nowrap;
        justify-content: flex-start;
        padding-left: 2rem;
        padding-right: 2rem;
        box-sizing: border-box;
        &.is-fixed {
            top: 6rem;
        }
    }
    @include susy-breakpoint($s-768, $c12) {
        height: 6.6rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        height: 6.6rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        height: 6.6rem;
    }
    &__btn {
        box-sizing: border-box;
        color: #fff;
        font-size: 1.6rem;
        text-align: center;
        margin: 0 2rem;
        transition: all 0.3s ease;
        height: 100%;
        position: relative;
        display: flex;
        align-items: center;
        &::after {
            content: "";
            width: 100%;
            height: 0.4rem;
            background-color: #ffd038;
            position: absolute;
            bottom: 0;
            left: 0;
            opacity: 0;
            transition: all 0.3s ease;
        }
        &:hover {
            text-decoration: none;
            color: #ffd038;
            &::after {
                opacity: 1;
            }
        }
        &.is-active {
            color: #ffd038;
            &::after {
                opacity: 1;
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.4rem;
            padding-top: 0.6rem;
            margin-left: 1rem;
            margin-right: 1rem;
            &::after {
                height: 0.2rem;
            }
        }
    }
}