.event-lottery {
    @extend .event-block;
    &__title {
        @extend .event-title;
    }
    &__content {
        @extend .event-wrap;
        padding-bottom: 6rem;
        display: block;
    }
    &__event-intro {
        padding: 0 8rem;
        @include susy-breakpoint($s-320, $c8) {
            padding-left: 0;
            padding-right: 0;
        }
        @include susy-breakpoint($s-768, $c12) {
            padding-left: 1rem;
            padding-right: 1rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            padding-left: 3rem;
            padding-right: 3rem;
        }
    }
    &__ul-list {
        list-style: none;
        margin: 0 0 3rem;
        padding: 0;
        li {
            position: relative;
            font-size: 2rem;
            line-height: 1.6;
            color: #fff;
            padding-left: 2rem;
            &::before {
                width: 1.6rem;
                height: 1.6rem;
                background-color: #ffdb6f;
                position: absolute;
                left: 0;
                top: 0.8rem;
                content: "";
                border-radius: 50%;
            }
            &:not(:last-child) {
                margin-bottom: 1rem;
            }
            strong {
                color: #ffdb6f;
                sup {
                    font-size: 0.5em;
                }
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
                &::before {
                    width: 1.4rem;
                    height: 1.4rem;
                    top: 0.4rem;
                }
            }
        }
    }
    &__link-wrap {
        display: flex;
        justify-content: center;
        @include susy-breakpoint($s-320, $c8) {
            flex-wrap: wrap;
            margin-bottom: 2rem;
            gap: 1rem;
        }
        .btn-link {
            margin-left: 2rem;
            margin-right: 2rem;
        }
    }
    &__prize-list {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: repeat(2, 1fr);
            margin-bottom: 4rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-template-columns: repeat(2, 1fr);
        }
    }
    &__prize-item {
        margin-bottom: 4rem;
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 0;
        }
        img {
            max-width: 100%;
            display: block;
            margin: 0 auto 1rem;
        }
        h3 {
            font-size: 1.8rem;
            color: #ffdb6f;
            line-height: 1.4;
            text-align: center;
            margin-bottom: 0;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
            }
        }
        h4 {
            font-size: 1.8rem;
            color: #fff;
            line-height: 1.4;
            text-align: center;
            margin-bottom: 0;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
            }
        }
        h5 {
            text-align: center;
            padding-top: 0.8rem;
            span {
                line-height: 1.6;
                font-size: 1.2rem;
                display: inline-block;
                padding: 1rem 1.6rem;
                border-radius: 6rem;
                background-color: #ffdb6f;
                color: #000;
                strong {
                    color: #ff0000;
                }
                @include susy-breakpoint($s-320, $c8) {
                    font-size: 1.2rem;
                    padding: 0.8rem 1.2rem;
                }
            }
        }
        h6 {
            font-size: 0.8rem;
            line-height: 1.6;
            color: #fff;
            text-align: center;
            transform: scale(0.8) translate3d(0,-10%,0);
        }
        &:nth-child(4) {
            h3 {
                margin-top: -0.3rem;
            }
        }
    }
    &__list {
        @extend .event-list;
        display: block;
        > li {
            flex: 1;
            padding: 0 1rem;
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            align-items: flex-start;
            @include susy-breakpoint($s-320, $c8) {
                flex-basis: 100%;
                align-items: flex-start;
                &:nth-child(1) {
                    margin-bottom: 1rem;
                }
            }
            &:not(:last-child) {
                margin-bottom: 2rem;
            }
            &.justify-center {
                justify-content: center;
            }
            > span {
                font-size: 2.4rem;
                line-height: 1.6;
                color: #fff;
                flex: 1;
                padding-top: 0.6rem;
                @include susy-breakpoint($s-320, $c8) {
                    font-size: 1.4rem;
                    padding-top: 0.4rem;
                }
                @include susy-breakpoint($s-768, $c12) {
                    font-size: 1.8rem;
                    padding-top: 0.2rem;
                }
                @include susy-breakpoint($s-960, $c12) {
                    font-size: 1.8rem;
                    padding-top: 0.6rem;
                }
                @include susy-breakpoint($s-1201, $c18) {
                    font-size: 1.8rem;
                    padding-top: 0.6rem;
                }
            }
        }
    }
    &__label {
        @extend .site-label;
    }
    &__business-title {
        img {
            height: 5rem;
            display: block;
            margin: 0 auto 2rem;
            @include susy-breakpoint($s-320, $c8) {
                height: 2.6rem;
            }
        }
    }
    &__business-wrap {
        position: relative;
        padding-bottom: 2rem;
        margin-bottom: 2rem;
        &::after {
            position: absolute;
            width: 100%;
            height: .2rem;
            background: linear-gradient(90deg,rgba(234,227,150,0),#eae396 30%,#eae396 50%,#eae396 70%,rgba(234,227,150,0));
            bottom: 0;
            left: 0;
            content: "";
        }
    }
    &__btn-toggle {
        width: 100%;
        background: rgb(3,65,114);
        background: linear-gradient(0deg, rgba(3,65,114,1) 0%, rgba(3,66,115,1) 17%, rgba(20,119,139,1) 50%, rgba(42,191,172,1) 100%);
        font-size: 3.2rem;
        color: #fff;
        display: flex;
        padding: 0.8rem 6.8rem;
        box-sizing: border-box;
        border: 0;
        align-items: center;
        margin-bottom: 2rem;
        cursor: pointer;
        position: relative;
        &::after {
            width: 1.9rem;
            height: 1.8rem;
            background: transparent url('../images/icon-arrow.svg') no-repeat 0 0;
            background-size: cover;
            top: 50%;
            right: 4rem;
            position: absolute;
            transform-origin: 50%;
            transform: translate3d(0,-50%,0) rotate(180deg);
            content: "";
            transition: all 300ms ease;
        }
        &.is-active {
            &::after {
                transform: translate3d(0,-50%,0) rotate(0deg);
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.8rem;
            padding: 0.8rem 2rem;
            &::after {
                right: 2rem;
                width: 1.9rem * 0.8;
                height: 1.8rem * 0.8;
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: 2.4rem;
            padding-left: 3rem;
        }
    }
    &__business-body {
        display: none;
        padding: 0 5rem;
        @include susy-breakpoint($s-320, $c8) {
            padding-left: 0;
            padding-right: 0;
        }
        @include susy-breakpoint($s-768, $c12) {
            padding-left: 2rem;
            padding-right: 2rem;
        }
    }
    &__inner-ol-list {
        flex-basis: 100%;
        list-style: none;
        margin: 0;
        padding: 1rem 0 0 2rem;
        counter-reset: inner-ol-list;
        @include susy-breakpoint($s-320, $c8) {
            padding-left: 1rem;
        }
        > li {
            counter-increment: inner-ol-list;
            padding-left: 4rem;
            position: relative;
            font-size: 2.4rem;
            color: #fff;
            line-height: 1.6;
            &:not(:last-child) {
                margin-bottom: 1rem;
            }
            &::before {
                position: absolute;
                content: counter(inner-ol-list);
                font-family: 'Anton', sans-serif;
                font-style: italic;
                color: #000;
                font-size: 2.4rem;
                width: 3.6rem;
                height: 3.6rem;
                border-radius: 50%;
                background-color: #ffdb6f;
                top: 0.2rem;
                left: 0;
                display: flex;
                align-items: center;
                box-sizing: border-box;
                padding-left: 1rem;
            }
            small {
                vertical-align: baseline;
                font-size: 2rem;
                color: #ff0000;
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
                padding-left: 2.4rem;
                &::before {
                    font-size: 1.6rem;
                    width: 2rem;
                    height: 2rem;
                    top: 0.2rem;
                    padding-left: 0.4rem;
                }
                small {
                    font-size: 1.2rem;
                }
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 1.8rem;
                padding-left: 3rem;
                &::before {
                    font-size: 1.6rem;
                    width: 2.4rem;
                    height: 2.4rem;
                    top: 0.2rem;
                    padding-left: 0.6rem;
                }
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 1.8rem;
                padding-left: 3rem;
                &::before {
                    font-size: 1.6rem;
                    width: 2.4rem;
                    height: 2.4rem;
                    top: 0.2rem;
                    padding-left: 0.6rem;
                }
            }
        }
    }
    &__link-wrap-bottom {
        padding-top: 4rem;
        display: flex;
        justify-content: center;
        margin-bottom: 6rem;
        @include susy-breakpoint($s-320, $c8) {
            display: block;
            padding-top: 2rem;
            margin-bottom: 2rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            padding-top: 3rem;
        }
    }
    &__btn-link {
        position: relative;
        display: block;
        margin: 0 2rem;
        @include susy-breakpoint($s-320, $c8) {
            padding-top: 1rem;
            padding-bottom: 1rem;
            text-align: center;
        }
        @include susy-breakpoint($s-768, $c12) {
            margin-left: 1rem;
            margin-right: 1rem;
        }
        img {
            position: absolute;
            width: 10.6rem;
            height: 9.2rem;
            top: 50%;
            left: 0;
            transform: translate3d(-10%,-50%,0);
            @include susy-breakpoint($s-320, $c8) {
                width: 10.6rem * 0.6;
                height: 9.2rem * 0.6;
                transform: translate3d(-10%,-50%,0);
            }
            @include susy-breakpoint($s-768, $c12) {
                width: 10.6rem * 0.6;
                height: 9.2rem * 0.6;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 10.6rem * 0.8;
                height: 9.2rem * 0.8;
            }
        }
        span {
            position: relative;
            display: inline-block;
            border: solid 0.2rem #ffdb6f;
            padding: 1rem 2rem 1rem 10rem;
            font-size: 2rem;
            color: #ffdb6f;
            border-radius: 6rem;
            transition: all 300ms ease;
            box-sizing: border-box;
            font-weight: 700;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
                padding: 0.6rem 1rem 0.6rem 6rem;
                width: 22rem
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 1.4rem;
                padding: 0.6rem 2rem 0.6rem 6rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 1.6rem;
                padding: 0.6rem 2rem 0.6rem 8rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: 1.8rem;
            }
        }
        &:hover {
            span {
                background-color: #ffdb6f;
                color: #000;
            }
        }
    }
}