.event-vr {
    @extend .event-block;
    &__title {
        @extend .event-title;
    }
    &__list {
        @extend .event-list;
        li {
            padding: 0 1rem;
            display: flex;
            align-items: center;
            @include susy-breakpoint($s-320, $c8) {
                flex-basis: 100%;
                justify-content: center;
            }
            span {
                font-size: 2.4rem;
                line-height: 1.6;
                color: #fff;
                @include susy-breakpoint($s-320, $c8) {
                    font-size: 1.4rem;
                }
                @include susy-breakpoint($s-768, $c12) {
                    font-size: 1.6rem;
                }
            }
        }
    }
    &__label {
        @extend .site-label;
    }
    &__h3 {
        color: #ffdb6f;
        font-size: 2.8rem;
        line-height: 1.6;
        text-align: center;
        margin-bottom: 1rem;
        strong {
            color: #99f538;
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.6rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: 2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 2.2rem;
        }
    }
    &__h4 {
        color: #fff;
        font-size: 2.8rem;
        line-height: 1.6;
        text-align: center;
        margin-bottom: 2rem;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.6rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: 2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 2.2rem;
        }
    }
    &__remind {
        text-align: center;
        margin-bottom: 4rem;
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 2rem;
        }
        span {
            display: inline-block;
            background: none;
            padding: 0.4rem 2rem;
            border: 0.4rem solid;
            font-size: 2.4rem;
            color: #eaca69;
            border-radius: 4rem;
            line-height: 1.6;
            background-origin: border-box;
            background-clip: content-box, border-box;
            strong {
                color: #99f538;
            }
            @include susy-breakpoint($s-320, $c8) {
                border-radius: 1rem;
                border-width: 0.2rem;
                font-size: 1.6rem;
                padding-left: 1rem;
                padding-right: 1rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 2rem;
            }
        }
    }
    &__content {
        @extend .event-wrap;
    }

    &__remarks {
        color: #ffae00;
        font-size: 2.2rem;
        line-height: 1.6;
        text-align: center;
        margin-bottom: 2rem;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.4rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: 1.8rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 2rem;
        }
    }
    &__rule {
        @extend .rule;
        h3 {
            @extend .rule-title;
        }
        ol {
            @extend .site-rule;
        }
    }
    &__item {
        display: flex;
        padding: 1rem 4rem 2rem;
        margin-bottom: 1rem;
        position: relative;
        align-items: center;
        @include susy-breakpoint($s-320, $c8) {
            padding-left: 0;
            padding-right: 0;
            flex-wrap: wrap;
            padding-top: 0;
        }
        @include susy-breakpoint($s-768, $c12) {
            padding-left: 2rem;
            padding-right: 2rem;
        }
        img {
            height: 2.4rem;
            display: inline-block;
            vertical-align: middle;
            margin-right: 0.6rem;
            @include susy-breakpoint($s-320, $c8) {
                height: 1.8rem;
            }
        }
        &::after {
            position: absolute;
            width: 100%;
            height: 0.1rem;
            background: linear-gradient(90deg, rgba(234,227,150,0) 0%, rgba(234,227,150,1) 30%, rgba(234,227,150,1) 50%, rgba(234,227,150,1) 70%, rgba(234,227,150,0) 100%);
            bottom: 0;
            left: 0;
            content: "";
        }
        span {
            font-size: 2.6rem;
            color: #fff;
            a {
                border-bottom: 0.1rem solid rgba(255,255,255,0.5);
                transition: all 300ms ease-in-out;
                text-decoration: none;
                color: #fff;
                &:hover {
                    border-bottom-color: #fff;
                }
            }
            &:nth-child(1) {
                flex-basis: 22%;
            }
            &:nth-child(2) {
                flex-grow: 1;
            }
            &:nth-child(3) {
                flex-basis: 25%;
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
                &:not(:last-child) {
                    margin-bottom: 0.8rem;
                }
                &:nth-child(1) {
                    flex-basis: 100%;
                }
                &:nth-child(3) {
                    flex-basis: 100%;
                }
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 1.8rem;
                &:nth-child(1) {
                    flex-basis: 16%;
                }
                &:nth-child(3) {
                    flex-basis: 30%;
                }
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 2rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: 2.2rem;
            }
            &.opening {
                a {
                    color: #ffdb6f;
                    border-bottom: 0.1rem solid rgba(#ffdb6f,0.5);
                    &:hover {
                        border-bottom-color: #ffdb6f;
                    }
                }
            }
        }
    }
    &__store {
        flex-basis: 100%;
    }
    &__store-header {
        box-sizing: border-box;
        padding: 0 4rem;
        width: 100%;
        height: 6rem;
        border-radius: 4rem;
        background: rgb(3,65,114);
        background: linear-gradient(90deg, rgba(3,65,114,1) 0%, rgba(41,190,172,1) 100%);
        display: flex;
        align-items: center;
        margin-bottom: 2rem;
        @include susy-breakpoint($s-320, $c8) {
            padding-left: 0;
            padding-right: 0;
            align-content: center;
            display: none;
        }
        @include susy-breakpoint($s-768, $c12) {
            padding-left: 2rem;
            padding-right: 2rem;
        }
        span {
            font-size: 2.6rem;
            color: #fff;
            &:nth-child(1) {
                flex-basis: 22%;
            }
            &:nth-child(2) {
                flex-grow: 1;
            }
            &:nth-child(3) {
                flex-basis: 25%;
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
                &:nth-child(1) {
                    flex-basis: 25%;
                }
                &:nth-child(3) {
                    flex-basis: 30%;
                }
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: 1.8rem;
                &:nth-child(1) {
                    flex-basis: 16%;
                }
                &:nth-child(3) {
                    flex-basis: 30%;
                }
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 2rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: 2.2rem;
            }
        }
    }
}