.event-video {
    padding-top: 8rem;
    padding-bottom: 12rem;
    @include susy-breakpoint($s-320, $c8) {
        padding-top: 4rem;
        padding-bottom: 6rem;
    }
    .video-container {
        padding-bottom: 56.25%;
        position: relative;
        iframe {
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
        }
    }
    .video-play {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    .swiper-slide {
        box-sizing: border-box;
        background-clip: padding-box;
        border: solid 4px transparent;
        border-radius: 1rem;
        transform: scale(0.85);
        transition: all 300ms ease;
        &:before {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: 0;
            margin: -4px;
            border-radius: inherit;
            background: linear-gradient(0deg, #edc36b, #eee698);
        }
        @include susy-breakpoint($s-320, $c8) {
            border-width: 2px;
            border-radius: 0.2rem;
            &:before {
                margin: -2px;
            }
        }
    }
    .swiper-slide-active {
        transform: scale(1);
    }
}

.video-container iframe,
.video-container object,
.video-container embed {
    pointer-events: none;
}

.video-swiper-wrapper {
    position: relative;
    box-sizing: border-box;
}

.swiper-button-next {
    background: transparent url('../images/btn-next@2x.png') no-repeat 0 0;
    background-size: cover;
    width: 5.2rem;
    height: 5.3rem;
    right: -8rem;
    &::after {
        display: none;
    }
}

.swiper-button-prev {
    background: transparent url('../images/btn-prev@2x.png') no-repeat 0 0;
    background-size: cover;
    width: 5.2rem;
    height: 5.3rem;
    left: -8rem;
    &::after {
        display: none;
    }
}

.swiper-pagination {
    width: 100%;
    padding-top: 2.6rem;
}

.swiper-pagination-bullet {
    width: 1.8rem;
    height: 1.8rem;
    background-color: #fff;
    opacity: 1;
    box-shadow: 0 0.2rem 0.4rem #000;
    margin: 0 1.5rem;
    @include susy-breakpoint($s-320, $c8) {
        width: 1.2rem;
        height: 1.2rem;
        margin-left: 1rem;
        margin-right: 1rem;
    }
}

.swiper-pagination-bullet-active {
    background-color: #eaca69;
}